import React, { Component } from 'react';
import { connect } from 'react-redux';

import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import IconButton from '@material-ui/core/IconButton/IconButton';
import AppBar from '@material-ui/core/AppBar/AppBar';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer/Drawer';
import Divider from '@material-ui/core/Divider/Divider';
import List from '@material-ui/core/List/List';
import ListItem from '@material-ui/core/ListItem/ListItem';

import { withLocalize, Translate } from 'react-localize-redux';

import Lockr from 'lockr';

import siteTranslations from '../translations/site.json';
import imgSS from '../images/soulstone.png';

import {
  SCREEN_PRE_SALE,
  SCREEN_MY_ACCOUNT,
  SCREEN_MY_HEROES,
  SCREEN_ALL_HEROES,
  SCREEN_SOUL_STONE,
  SCREEN_ITEMS,
  SCREEN_PVP,
  SCREEN_PVE,
  SCREEN_PROFESSIONS,
  setScreen,
} from '../actions/app';

class WalletAppBar extends Component {
  constructor(props) {
    super(props);

    const languages = [
      { name: 'English', code: 'en' },
      { name: '简体中文', code: 'cn' },
    ];
    this.state = {
      languages,
      mobileOpen: false,
    };

    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    const activeLanguageCode = Lockr.get('activeLanguage') || 'en';
    this.props.setActiveLanguage(activeLanguageCode);
  }

  setActiveLanguage = code => {
    this.props.setActiveLanguage(code);
    Lockr.set('activeLanguage', code);
    this.setState({ mobileOpen: false });
  };

  getLanguageFlag = code => {
    return `images/languages/${code}.png`;
  };

  navigateTo = screen => {
    const { setScreen } = this.props;
    this.setState({ mobileOpen: false });
    setScreen(screen);
  };

  home = () => {
    this.navigateTo(SCREEN_PRE_SALE);
  };

  myHeroes = () => {
    this.navigateTo(SCREEN_MY_HEROES);
  };

  showAllHeroes = () => {
    this.navigateTo(SCREEN_ALL_HEROES);
  };

  showItems = () => {
    this.navigateTo(SCREEN_ITEMS);
  };

  showPVE = () => {
    this.navigateTo(SCREEN_PVE);
  };
  showPVP = () => {
    this.navigateTo(SCREEN_PVP);
  };

  showProfessions = () => {
    this.navigateTo(SCREEN_PROFESSIONS);
  };

  showSoulStone = () => {
    this.navigateTo(SCREEN_SOUL_STONE);
  };

  myAccount = () => {
    this.navigateTo(SCREEN_MY_ACCOUNT);
  };

  handleDrawerToggle = () => {
    this.setState(state => ({ mobileOpen: !state.mobileOpen }));
  };

  renderDesktop() {
    const { languages } = this.state;
    return (
      <div className="tool-bar desktop-app-bar">
        <div className="logo-container">
          <IconButton
            color="inherit"
            aria-label={<Translate id="general.tronlegend" />}
            onClick={this.home}
          >
            <span className="glowing-pink">
              <Translate id="general.tronlegend" />
            </span>
          </IconButton>
        </div>

        <div>
          <Tooltip title={<Translate id="general.soulstone" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.soulstone" />}
              onClick={this.showSoulStone}
            >
              <span className="menu-link glowing-gold">
                <img
                  src={imgSS}
                  alt={<Translate id="general.soulstone" />}
                  className="image-ss-menu"
                />
                <Translate id="general.soulstone" />
              </span>
            </IconButton>
          </Tooltip>

          <Tooltip title={<Translate id="general.heroes" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.heroes" />}
              onClick={this.showAllHeroes}
            >
              <span className="menu-link glowing-red">
                <Translate id="general.heroes" />
              </span>
            </IconButton>
          </Tooltip>

          <Tooltip title={<Translate id="general.items" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.items" />}
              onClick={this.showItems}
            >
              <span className="menu-link glowing-blue">
                <Translate id="general.items" />
              </span>
            </IconButton>
          </Tooltip>
          <Tooltip title={<Translate id="general.pve" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.pve" />}
              onClick={this.showPVE}
            >
              <span className="menu-link glowing-blue">
                <Translate id="general.pve" />
              </span>
            </IconButton>
          </Tooltip>
          <Tooltip title={<Translate id="general.pvp" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.pvp" />}
              onClick={this.showPVP}
            >
              <span className="menu-link glowing-blue">
                <Translate id="general.pvp" />
              </span>
            </IconButton>
          </Tooltip>
          <Tooltip title={<Translate id="general.professions" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.professions" />}
              onClick={this.showProfessions}
            >
              <span className="menu-link glowing-blue">
                <Translate id="general.professions" />
              </span>
            </IconButton>
          </Tooltip>
          <Tooltip title={<Translate id="general.myheroes" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.myheroes" />}
              onClick={this.myHeroes}
            >
              <span className="menu-link glowing-blue">
                <Translate id="general.myheroes" />
              </span>
            </IconButton>
          </Tooltip>

          <Tooltip title={<Translate id="general.myaccount" />}>
            <IconButton
              color="inherit"
              aria-label={<Translate id="general.myaccount" />}
              onClick={this.myAccount}
            >
              <span className="menu-link glowing-blue">
                <Translate id="general.myaccount" />
              </span>
            </IconButton>
          </Tooltip>

          {languages.map(lang => (
            <Tooltip key={lang.code} title={lang.name}>
              <IconButton
                id={lang.code}
                color="inherit"
                onClick={() => this.setActiveLanguage(lang.code)}
              >
                <img
                  src={this.getLanguageFlag(lang.code)}
                  className="image-flag"
                  alt="lang"
                />
              </IconButton>
            </Tooltip>
          ))}
        </div>
      </div>
    );
  }

  renderMobile() {
    const { languages } = this.state;
    return (
      <div className="tool-bar mobile-app-bar">
        <IconButton
          color="inherit"
          aria-label="Open drawer"
          onClick={this.handleDrawerToggle}
        >
          <MenuIcon />
        </IconButton>
        <div className="logo-container">
          <IconButton
            color="inherit"
            aria-label={<Translate id="general.tronlegend" />}
            onClick={this.home}
          >
            <span className="glowing-pink">
              <Translate id="general.tronlegend" />
            </span>
          </IconButton>
        </div>
        <div className="mobile-app-bar-right" />
        <Drawer
          container={this.props.container}
          variant="temporary"
          anchor="left"
          open={this.state.mobileOpen}
          onClose={this.handleDrawerToggle}
          className="drawer"
        >
          <div className="drawer-menu">
            <List>
              <ListItem
                button
                key="home"
                onClick={this.home}
                className="drawer-item"
              >
                <span className="menu-link glowing-pink">
                  <Translate id="general.presale" />
                </span>
              </ListItem>
              <ListItem
                button
                key="soulstone"
                onClick={this.showSoulStone}
                className="drawer-item"
              >
                <span className="menu-link glowing-gold">
                  <Translate id="general.soulstone" />
                </span>
              </ListItem>
              <ListItem
                button
                key="heroes"
                onClick={this.showAllHeroes}
                className="drawer-item"
              >
                <span className="menu-link glowing-red">
                  <Translate id="general.heroes" />
                </span>
              </ListItem>
              <ListItem
                button
                key="items"
                onClick={this.showItems}
                className="drawer-item"
              >
                <span className="menu-link glowing-blue">
                  <Translate id="general.items" />
                </span>
              </ListItem>
              <ListItem
                button
                key="pve"
                onClick={this.showPVE}
                className="drawer-item"
              >
                <span className="menu-link glowing-blue">
                  <Translate id="general.pve" />
                </span>
              </ListItem>
              <ListItem
                button
                key="pvp"
                onClick={this.showPVP}
                className="drawer-item"
              >
                <span className="menu-link glowing-blue">
                  <Translate id="general.pvp" />
                </span>
              </ListItem>
              <ListItem
                button
                key="professions"
                onClick={this.showProfessions}
                className="drawer-item"
              >
                <span className="menu-link glowing-blue">
                  <Translate id="general.professions" />
                </span>
              </ListItem>
              <Divider />
              <ListItem
                button
                key="myheroes"
                onClick={this.myHeroes}
                className="drawer-item"
              >
                <span className="menu-link glowing-blue">
                  <Translate id="general.myheroes" />
                </span>
              </ListItem>
              <ListItem
                button
                key="myaccount"
                onClick={this.myAccount}
                className="drawer-item"
              >
                <span className="menu-link glowing-blue">
                  <Translate id="general.myaccount" />
                </span>
              </ListItem>
              <Divider />
              <ListItem button key="languages" className="drawer-item">
                {languages.map(lang => (
                  <Tooltip key={lang.code} title={lang.name}>
                    <IconButton
                      id={lang.code}
                      color="inherit"
                      onClick={() => this.setActiveLanguage(lang.code)}
                    >
                      <img
                        src={this.getLanguageFlag(lang.code)}
                        className="image-flag"
                        alt="lang"
                      />
                    </IconButton>
                  </Tooltip>
                ))}
              </ListItem>
            </List>
          </div>
        </Drawer>
      </div>
    );
  }

  render() {
    return (
      <AppBar position="sticky">
        <Toolbar>
          {this.renderDesktop()}
          {this.renderMobile()}
        </Toolbar>
      </AppBar>
    );
  }
}

const mapDispatchToProps = {
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(WalletAppBar));
