import React from 'react';
import { connect } from 'react-redux';

import { MuiThemeProvider } from '@material-ui/core/styles';

import { withLocalize, Translate } from 'react-localize-redux';
import siteTranslations from '../translations/site.json';

import CountUp from 'react-countup';

import AOS from 'aos';
import 'aos/dist/aos.css';

import { showSnackbar } from '../actions/snackbar';

import customTheme from './customTheme';

import { setScreen } from '../actions/app';

import Tron from '../utils/tron';

AOS.init({
  once: true, // whether animation should happen only once - while scrolling down
  mirror: false, // whether elements should animate out while scrolling past them
});

class GlobalStats extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      globalClaimedDividend: null,
      globalReferral: null,
    };
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    await Tron.initTronWeb();

    const globalData = await Tron.getGlobalData();
    this.setState({ globalClaimedDividend: globalData.dividend });
    this.setState({ globalReferral: globalData.referral });
  }

  render() {
    const { globalClaimedDividend, globalReferral } = this.state;

    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="global-stats">
          <Translate id="presale.globaldividends" />:{' '}
          <span className="glowing-red">
            <CountUp start={0} end={Tron.sunToTrx(globalClaimedDividend)} /> TRX
          </span>{' '}
          <Translate id="presale.globalreferrals" />:{' '}
          <span className="glowing-red">
            <CountUp start={0} end={Tron.sunToTrx(globalReferral)} /> TRX
          </span>
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = {
  showSnackbar,
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(GlobalStats));
