import React, { Component } from 'react';
import { connect } from 'react-redux';

import { withLocalize } from 'react-localize-redux';

import { renderToStaticMarkup } from 'react-dom/server';

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import ReactGA from 'react-ga';

import { setScreen, SCREEN_PRE_SALE } from '../actions/app';
import customTheme from './customTheme';
import AppBar from './WalletAppBar';
import GlobalStats from './GlobalStats';
import Snackbar from './WalletSnackbar';
import RoutingScreen from './RoutingScreen';
import Footer from './Footer';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.props.initialize({
      languages: [
        { name: 'English', code: 'en' },
        { name: 'Chinese', code: 'cn' },
      ],
      options: { renderToStaticMarkup },
    });
  }

  componentDidMount() {
    this.initWallet();
    this.initReactGA();
  }

  initWallet = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_PRE_SALE);
  };

  initReactGA = () => {
    ReactGA.initialize('UA-134341340-1');
    ReactGA.pageview('/home');
  };

  render() {
    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="App">
          <AppBar />
          <div id="main-body">
            <GlobalStats />
            <RoutingScreen />
          </div>
          <Footer />
          <Snackbar />
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = {
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(App));
