import TronWeb from 'tronweb';

import store from '../store';
import { showSnackbar } from '../actions/snackbar';
import { reloadAccount } from '../actions/app';
import { SNACKBAR_ERROR, SNACKBAR_INFO } from '../components/WalletSnackbar';

// // TESTNET
// const CONTRACT_ADDRESS = '41385a59fb352d48e1ed17b57c9ae0aa47f48c3fe1';
// const TRONGRID_API = 'https://api.shasta.trongrid.io';
// const gasLimit = 100 * 1000000; // 100 trx

// // MAINNET
const CONTRACT_ADDRESS = '4190ddba073f375a26be938ec7798e1c06716701b8';
const TRONGRID_API = 'https://api.trongrid.io';
const gasLimit = 50 * 1000000; // 50 trx

export const TRON_WEB_STATE_NOT_INSTALLED = 'TRON_WEB_STATE_NOT_INSTALLED';
export const TRON_WEB_STATE_LOCKED = 'TRON_WEB_STATE_LOCKED';
export const TRON_WEB_STATE_UNLOCKED = 'TRON_WEB_STATE_UNLOCKED';

export const FOUNDATION_ADDRESS = 'TMiT1foMn3D9TysDXeZqVM5Fk4Pbwvfa2d';

const injectTronWeb = async () => {
  return new Promise(resolve => {
    let tronWebState = TRON_WEB_STATE_NOT_INSTALLED;

    // Try to inject it
    let tries = 0;
    const timer = setInterval(() => {
      if (tries >= 10) {
        if (!window.tronWeb) {
          window.tronWeb = defaultTronWeb;
        }
        clearInterval(timer);
        return resolve(tronWebState);
      }

      if (!!window.tronWeb && window.tronWeb !== defaultTronWeb) {
        tronWebState = TRON_WEB_STATE_LOCKED;

        if (window.tronWeb.ready) {
          tronWebState = TRON_WEB_STATE_UNLOCKED;

          // Already have it
          clearInterval(timer);
          return resolve(tronWebState);
        }
      }

      tries++;
    }, 100);
  });
};

let currentAddress;
let contract;
let tronWebState;
const monitorAddressChanged = () => {
  window.tronWeb.on('addressChanged', async address => {
    if (currentAddress !== address.base58) {
      currentAddress = address.base58;
      tronWebState = await injectTronWeb();
      contract = await initContract();
      store.dispatch(showSnackbar('message.addressChanged', SNACKBAR_INFO));
      store.dispatch(reloadAccount());
    }
  });
};

const initDefaultTronWeb = () => {
  const defaultTronWeb = new TronWeb(TRONGRID_API, TRONGRID_API, TRONGRID_API);

  // Set default address (foundation address) used for contract calls
  // Directly overwrites the address object as TronLink disabled the
  // function call
  defaultTronWeb.defaultAddress = {
    hex: defaultTronWeb.address.toHex(FOUNDATION_ADDRESS),
    base58: FOUNDATION_ADDRESS,
  };

  return defaultTronWeb;
};

const defaultTronWeb = initDefaultTronWeb();

const initContract = async () => {
  let contract;
  if (tronWebState !== TRON_WEB_STATE_LOCKED) {
    contract = await window.tronWeb.contract().at(CONTRACT_ADDRESS);
  } else {
    contract = await defaultTronWeb.contract().at(CONTRACT_ADDRESS);
  }
  return contract;
};

export default {
  async initTronWeb() {
    tronWebState = await injectTronWeb();
    contract = await initContract();
    monitorAddressChanged();

    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_INFO));
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_INFO));
    }
    return tronWebState;
  },

  async getGlobalData() {
    const dividend = await contract.getGlobalClaimedDividend().call();
    const referral = await contract.globalReferral().call();
    return {
      dividend: dividend ? dividend.toNumber() : 0,
      referral: referral ? referral.toNumber() : 0,
    };
  },

  async getPlayerData() {
    const playerData = await contract.getPlayerData().call();
    return {
      address: currentAddress,
      stones: playerData.stones.toNumber(),
      claimedDividend: playerData.claimedDividend.toNumber(),
      claimableDividend: playerData.claimableDividend.toNumber(),
      referral: playerData.referral.toNumber(),
      lastPurchaseId: playerData.lastPurchaseId.toNumber(),
      lastPurchaseSize: playerData.lastPurchaseSize,
    };
  },

  async balanceOf(address) {
    const balance = await contract.balanceOf(address).call();
    return balance;
  },

  async currentAddressBalance() {
    const balance = await window.tronWeb.trx.getBalance();
    return balance;
  },

  async getPlayerCards(page) {
    const cards = await contract.getPlayerCards(page).call();
    return cards;
  },

  async getPlayerCardsLastSize(size) {
    const cards = await contract.getPlayerCardsLastSize(size).call();
    return cards;
  },

  async claimDividend() {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }

    const payAmount = 0;
    const data = await contract.claimDividend().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: true,
    });

    return data;
  },

  async getCommonPrice() {
    const price = await contract.getBasePrice().call();
    return price.toNumber();
  },

  async getRarePrice() {
    const price = await contract.getRarePrice().call();
    return price.toNumber();
  },

  async getEpicPrice() {
    const price = await contract.getEpicPrice().call();
    return price.toNumber();
  },

  async getLegendaryPrice() {
    const price = await contract.getLegendaryPrice().call();
    return price.toNumber();
  },

  async purchase(packsNumber, packType, referrer) {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }

    let price = 0;
    if (packType === '0') {
      price = await contract.getBasePrice().call();
    } else if (packType === '1') {
      price = await contract.getRarePrice().call();
    } else if (packType === '2') {
      price = await contract.getEpicPrice().call();
    } else if (packType === '3') {
      price = await contract.getLegendaryPrice().call();
    }

    const payAmount = price.toNumber() * packsNumber;
    const data = await contract.purchase(packsNumber, packType, referrer).send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: true,
    });
    return data;
  },

  sunToTrx(sun) {
    if (isNaN(sun)) {
      return '';
    }
    return sun / 1000000;
  },

  isValidAddress(address) {
    return address && address[0] === 'T' && address.length === 34;
  },
};
