import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withLocalize, Translate } from 'react-localize-redux';
import siteTranslations from '../translations/site.json';
import heroTranslations from '../translations/hero.json';

import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';

import ReactGA from 'react-ga';

import { showSnackbar } from '../actions/snackbar';

import { setScreen } from '../actions/app';

import Tron from '../utils/tron';

import presetHeroes from '../config/hero';
import presetSkills from '../config/skill';

import AOS from 'aos';
import 'aos/dist/aos.css';

AOS.init({
  once: true, // whether animation should happen only once - while scrolling down
  mirror: false, // whether elements should animate out while scrolling past them
});

class AllHeroes extends Component {
  constructor(props) {
    super(props);

    this.state = {
      purchases: null,
      totalHeroes: 0,
      heroes: [],
      cards: [],
      heroGroups: [],
      lastUpdatedTime: null,
    };
    this.props.addTranslation(siteTranslations);
    this.props.addTranslation(heroTranslations);
  }

  async componentDidMount() {
    await Tron.initTronWeb();
    this.loadCards();
    this.loadCardsGroup();
    window.addEventListener('load', AOS.refresh);

    ReactGA.pageview('/heroes');
  }

  getPlayerCardsGroup = () => {
    const ids = [];
    const protos = [];
    const purities = [];

    for (let i = 0; i < presetHeroes.length; i++) {
      ids.push(presetHeroes[i].id);
      protos.push(presetHeroes[i].id);
      purities.push(0);
    }

    const heroes = {
      ids,
      protos,
      purities,
    };

    this.setState({ heroes });
  };

  loadCards = () => {
    this.setState({ totalHeroes: 30 });
    this.getPlayerCardsGroup();
  };

  loadCardsGroup = () => {
    const { heroes } = this.state;
    const cards = heroes;

    const { heroGroups } = this.state;
    if (cards) {
      const ids = cards.ids;
      const protos = cards.protos;
      const purities = cards.purities;
      if (ids) {
        for (let i = 0; i < ids.length; i++) {
          const id = ids[i];
          const proto = protos[i];
          const purity = purities[i];
          console.log(proto);
          const heroData = this.getHeroData(proto);
          const rarity = heroData.ra;

          let foundRarityIndex = -1;
          let foundHero = false;
          for (let j = 0; j < heroGroups.length; j++) {
            const heroGroup = heroGroups[j];
            if (heroGroup.rarity === rarity) {
              // found Rarity
              foundRarityIndex = j;
              if (heroGroup.heroes) {
                for (let k = 0; k < heroGroup.heroes.length; k++) {
                  const hero = heroGroup.heroes[k];

                  if (hero.proto === proto) {
                    // found
                    foundHero = true;

                    if (hero.bestPurity < purity) {
                      hero.bestPurity = purity;
                      hero.bestId = id;
                    }

                    hero.count += 1;
                  }
                }
              }
            }
          }

          if (!foundHero) {
            //push card
            const hero = {
              proto: proto,
              bestPurity: purity,
              bestId: id,
              count: 1,
            };

            if (foundRarityIndex > -1) {
              // push to existing group
              const heroGroup = heroGroups[foundRarityIndex];
              heroGroup.heroes.push(hero);
            } else {
              const newHeroes = [];
              newHeroes.push(hero);

              const heroGroup = {
                rarity: rarity,
                heroes: newHeroes,
              };
              heroGroups.push(heroGroup);
            }
          } // if hero not found
        } // for i loop
      } // if ids
    } // if cards

    this.setState({ heroGroups });
  };

  Comparator = (a, b) => {
    if (a.rarity < b.rarity) return 1;
    if (a.rarity > b.rarity) return -1;
    return 0;
  };

  reorderHeroGroups = heroGroups => {
    return heroGroups.sort(this.Comparator);
  };

  getHeroData = id => {
    for (let i = 0; i < presetHeroes.length; i++) {
      if (presetHeroes[i].id === id) {
        return presetHeroes[i];
      }
    }
    return null;
  };

  getHeroSkills = ids => {
    let skills = [];
    if (ids) {
      for (let i = 0; i < ids.length; i++) {
        let id = ids[i];

        for (let j = 0; j < presetSkills.length; j++) {
          if (presetSkills[j].id === id) {
            skills.push(presetSkills[j]);
          }
        }
      }
    }

    return skills;
  };

  getAttackImage = rarity => {
    switch (rarity) {
      case 0:
        return 'images/misc/sword-common.png';
      case 1:
        return 'images/misc/sword-rare.png';
      case 2:
        return 'images/misc/sword-epic.png';
      case 3:
        return 'images/misc/sword-legendary.png';
      case 4:
        return 'images/misc/sword-mythic.png';
      default:
        return 'images/misc/sword-special.png';
    }
  };

  getDefenceImage = rarity => {
    switch (rarity) {
      case 0:
        return 'images/misc/shield-common.png';
      case 1:
        return 'images/misc/shield-rare.png';
      case 2:
        return 'images/misc/shield-epic.png';
      case 3:
        return 'images/misc/shield-legendary.png';
      case 4:
        return 'images/misc/shield-mythic.png';
      default:
        return 'images/misc/shield-special.png';
    }
  };

  getHealthImage = rarity => {
    switch (rarity) {
      case 0:
        return 'images/misc/heart-common.png';
      case 1:
        return 'images/misc/heart-rare.png';
      case 2:
        return 'images/misc/heart-epic.png';
      case 3:
        return 'images/misc/heart-legendary.png';
      case 4:
        return 'images/misc/heart-mythic.png';
      default:
        return 'images/misc/heart-special.png';
    }
  };

  getHealingImage = rarity => {
    switch (rarity) {
      case 0:
        return 'images/misc/healing-common.png';
      case 1:
        return 'images/misc/healing-rare.png';
      case 2:
        return 'images/misc/healing-epic.png';
      case 3:
        return 'images/misc/healing-legendary.png';
      case 4:
        return 'images/misc/healing-mythic.png';
      default:
        return 'images/misc/healing-special.png';
    }
  };

  getPlateCss = rarity => {
    switch (rarity) {
      case 0:
        return 'plate-green';
      case 1:
        return 'plate-blue';
      case 2:
        return 'plate-pink';
      case 3:
        return 'plate-gold';
      case 4:
        return 'plate-red';
      default:
        return 'plate-silver';
    }
  };

  getSkillCss = rarity => {
    switch (rarity) {
      case 0:
        return 'skill-green';
      case 1:
        return 'skill-blue';
      case 2:
        return 'skill-pink';
      case 3:
        return 'skill-gold';
      case 4:
        return 'skill-red';
      default:
        return 'skill-silver';
    }
  };

  getRarityClass = rarity => {
    switch (rarity) {
      case 0:
        return 'Common';
      case 1:
        return 'Rare';
      case 2:
        return 'Epic';
      case 3:
        return 'Legendary';
      case 4:
        return 'Mythic';
      default:
        return 'Common';
    }
  };

  getRarityText = rarity => {
    switch (rarity) {
      case 0:
        return 'general.common';
      case 1:
        return 'general.rare';
      case 2:
        return 'general.epic';
      case 3:
        return 'general.legendary';
      case 4:
        return 'general.mythic';
      default:
        return 'general.common';
    }
  };

  getRoleImage = role => {
    switch (role) {
      case 0:
        return 'images/misc/tank.png';
      case 1:
        return 'images/misc/damage.png';
      case 2:
        return 'images/misc/healer.png';
      default:
        return 'images/misc/damage.png';
    }
  };

  render() {
    const { totalHeroes, heroGroups } = this.state;

    const heroGroupsReordered = this.reorderHeroGroups(heroGroups);

    let totalCount = 0;
    return (
      <div className="cards">
        <div className="space" />
        <div color="primary" className="card game-card">
          <Typography variant="h4" color="secondary">
            <span className="glowing-pink white">
              <Translate id="general.allheroes" />{' '}
              {totalHeroes > 0 ? `x${totalHeroes}` : ''}
            </span>
          </Typography>

          <div className="my-account">
            <Grid container spacing={24}>
              {heroGroupsReordered.map(heroGroup => {
                return heroGroup.heroes.map(hero => {
                  const heroName = `hero.${hero.proto}n`;
                  const heroData = this.getHeroData(hero.proto);
                  const heroImage = `images/hero/${hero.proto}.png`;

                  const attackImage = this.getAttackImage(heroData.ra);
                  const defenceImage = this.getDefenceImage(heroData.ra);
                  const healthImage = this.getHealthImage(heroData.ra);
                  const healingImage = this.getHealingImage(heroData.ra);

                  const skills = this.getHeroSkills(heroData.s);

                  const heroCount = hero.count;

                  totalCount = totalCount + heroCount;

                  const plateCss = this.getPlateCss(heroData.ra);
                  const skillBackgroundCss = this.getSkillCss(heroData.ra);
                  const roleImageSrc = this.getRoleImage(heroData.rl);
                  const raritycla = this.getRarityClass(heroData.ra);
                  const rarityText = this.getRarityText(heroData.ra);
                  const rarityClass = `hero-rarity ${raritycla}`;

                  const bgClass = `bg-${hero.proto}`;

                  const boxCss = `hero-plate card ${plateCss}`;
                  const skillCss = `hero-skill card ${skillBackgroundCss}`;

                  return (
                    <Grid item xs={12} sm={6} md={3} lg={3} className="card">
                      <div
                        className="hero-placeholder"
                        data-aos="flip-right"
                        data-aos-delay="50"
                        data-aos-anchor-placement="top-center"
                      >
                        <div className="hero-role">
                          <img
                            className="image-skill-small"
                            src={roleImageSrc}
                            alt="hero"
                          />
                        </div>
                        <div className={rarityClass}>
                          <Translate id={rarityText} />
                        </div>
                        <div className={boxCss}>
                          <div className={bgClass}>
                            <img
                              className="image-hero-small"
                              src={heroImage}
                              alt="hero"
                            />
                          </div>

                          <div className="space" />
                          <div className="hero-name-banner">
                            <Translate id={heroName} />
                          </div>

                          <div className="hero-stats-plate">
                            {heroData.hl > 0 && (
                              <Grid container spacing={0}>
                                <Grid item xs={3} sm={3} md={3} lg={3}>
                                  <div className="hero-stats">
                                    <img
                                      className="image-stats-small"
                                      src={attackImage}
                                      alt="hero"
                                    />
                                    <div className="hero-stats-title">
                                      {heroData.a}
                                    </div>
                                  </div>
                                </Grid>
                                <Grid item xs={3} sm={3} md={3} lg={3}>
                                  <div className="hero-stats">
                                    <img
                                      className="image-stats-small"
                                      src={defenceImage}
                                      alt="hero"
                                    />
                                    <div className="hero-stats-title">
                                      {heroData.d}
                                    </div>
                                  </div>
                                </Grid>
                                <Grid item xs={3} sm={3} md={3} lg={3}>
                                  <div className="hero-stats">
                                    <img
                                      className="image-stats-small"
                                      src={healthImage}
                                      alt="hero"
                                    />
                                    <div className="hero-stats-title">
                                      {heroData.he}
                                    </div>
                                  </div>
                                </Grid>
                                <Grid item xs={3} sm={3} md={3} lg={3}>
                                  <div className="hero-stats">
                                    <img
                                      className="image-stats-small"
                                      src={healingImage}
                                      alt="hero"
                                    />
                                    <div className="hero-stats-title">
                                      {heroData.hl}
                                    </div>
                                  </div>
                                </Grid>
                              </Grid>
                            )}

                            {heroData.hl === 0 && (
                              <Grid container spacing={0}>
                                <Grid item xs={4} sm={4} md={4} lg={4}>
                                  <div className="hero-stats">
                                    <img
                                      className="image-stats-small"
                                      src={attackImage}
                                      alt="hero"
                                    />
                                    <div className="hero-stats-title">
                                      {heroData.a}
                                    </div>
                                  </div>
                                </Grid>
                                <Grid item xs={4} sm={4} md={4} lg={4}>
                                  <div className="hero-stats">
                                    <img
                                      className="image-stats-small"
                                      src={defenceImage}
                                      alt="hero"
                                    />
                                    <div className="hero-stats-title">
                                      {heroData.d}
                                    </div>
                                  </div>
                                </Grid>
                                <Grid item xs={4} sm={4} md={4} lg={4}>
                                  <div className="hero-stats">
                                    <img
                                      className="image-stats-small"
                                      src={healthImage}
                                      alt="hero"
                                    />
                                    <div className="hero-stats-title">
                                      {heroData.he}
                                    </div>
                                  </div>
                                </Grid>
                              </Grid>
                            )}
                          </div>

                          <div className={skillCss}>
                            <span className="hero-skill-banner">
                              <Translate id="hero.skill" />
                            </span>
                            {skills.map(skill => {
                              const imageSrc = `images/skill/${skill.id}.png`;
                              const skillName = `skill.${skill.id}n`;
                              const skillDesc = `skill.${skill.id}d`;
                              return (
                                <div className="hero-skill-item">
                                  <div className="icon">
                                    <img
                                      className="image-skill-small"
                                      src={imageSrc}
                                      alt="hero"
                                    />
                                  </div>

                                  <div className="description text-shadow">
                                    <div className="title text-shadow">
                                      <Translate id={skillName} />
                                    </div>
                                    <Translate id={skillDesc} />
                                  </div>
                                </div>
                              );
                            })}
                            <div className="space" />
                          </div>
                        </div>
                      </div>
                    </Grid>
                  );
                });
              })}
            </Grid>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = {
  setScreen,
  showSnackbar,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(AllHeroes));
