import React from 'react';
import { connect } from 'react-redux';

import { MuiThemeProvider } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';
import Button from '@material-ui/core/Button/Button';

import { withLocalize, Translate } from 'react-localize-redux';
import siteTranslations from '../translations/site.json';

import AOS from 'aos';
import 'aos/dist/aos.css';

import qs from 'qs';
import Lockr from 'lockr';
import ReactGA from 'react-ga';

import imgScroll from '../images/scroll.png';
import imgDragonWarrior from '../images/dragonwarrior.png';
import imgSS from '../images/soulstone.png';

import { showSnackbar } from '../actions/snackbar';

import customTheme from './customTheme';

import { SCREEN_MY_HEROES, setScreen } from '../actions/app';

import Tron, { FOUNDATION_ADDRESS } from '../utils/tron';
import {
  SNACKBAR_ERROR,
  SNACKBAR_INFO,
  SNACKBAR_SUCCESS,
} from './WalletSnackbar';

AOS.init({
  once: true, // whether animation should happen only once - while scrolling down
  mirror: false, // whether elements should animate out while scrolling past them
});

class PreSale extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      game: 1,
      sendAmount: '1',
      scrollType: '0',
      isLoading: false,
      commonPrice: null,
      rarePrice: null,
      epicPrice: null,
      legendaryPrice: null,
    };
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    this.parseReferralLink();

    const tronWebState = await Tron.initTronWeb();
    console.log(tronWebState);

    await this.getAllPrices();

    ReactGA.pageview('/pre-sale');
  }

  getAllPrices = async () => {
    const commonPrice = await Tron.getCommonPrice();
    const rarePrice = await Tron.getRarePrice();
    const epicPrice = await Tron.getEpicPrice();
    const legendaryPrice = await Tron.getLegendaryPrice();

    this.setState({ commonPrice });
    this.setState({ rarePrice });
    this.setState({ epicPrice });
    this.setState({ legendaryPrice });
  };

  parseReferralLink = () => {
    const storedReferralAddress = Lockr.get('referralAddress');
    if (storedReferralAddress) {
      this.setState({ referralAddress: storedReferralAddress });
      return;
    }

    const parsedQueryString = qs.parse(window.location.search.substr(1));
    let referralAddress = parsedQueryString['r'];
    if (!referralAddress || !Tron.isValidAddress(referralAddress)) {
      referralAddress = FOUNDATION_ADDRESS;
    }
    this.setState({ referralAddress });
    if (referralAddress !== FOUNDATION_ADDRESS) {
      Lockr.set('referralAddress', referralAddress);
    }
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  roll1 = () => {
    this.setState({ sendAmount: '1' });
  };
  roll10 = () => {
    this.setState({ sendAmount: '10' });
  };
  roll25 = () => {
    this.setState({ sendAmount: '25' });
  };
  roll5 = () => {
    this.setState({ sendAmount: '5' });
  };

  scrollCommon = () => {
    this.setState({ scrollType: '0' });
  };
  scrollRare = () => {
    this.setState({ scrollType: '1' });
  };
  scrollEpic = () => {
    this.setState({ scrollType: '2' });
  };
  scrollLegendary = () => {
    this.setState({ scrollType: '3' });
  };

  purchaseScroll = async () => {
    const { sendAmount, scrollType, referralAddress } = this.state;
    const { setScreen, showSnackbar } = this.props;
    console.log(referralAddress);
    try {
      showSnackbar('message.actionInProgress', SNACKBAR_INFO);
      const result = await Tron.purchase(
        sendAmount,
        scrollType,
        referralAddress
      );
      if (!result) {
        return;
      }
      Lockr.set('hasNewPurchase', true);
      showSnackbar('message.purchaseSucceed', SNACKBAR_SUCCESS);
      setScreen(SCREEN_MY_HEROES);
    } catch (e) {
      console.log(e);
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_INFO);
      } else {
        if (e.error && e.message) {
          if (
            e.error === 'CONTRACT_VALIDATE_ERROR' &&
            e.message.indexOf('balance is not sufficient') > 0
          ) {
            showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  render() {
    const {
      sendAmount,
      scrollType,
      commonPrice,
      rarePrice,
      epicPrice,
      legendaryPrice,
    } = this.state;

    const amount = parseInt(sendAmount, 10);
    let btn1Class =
      amount === 1 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btn10Class =
      amount === 10 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btn5Class =
      amount === 5 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btn25Class =
      amount === 25 ? 'glowing-blue medium-text' : 'glowing-silver';

    const scroll = parseInt(scrollType, 10);
    let btnCommonClass =
      scroll === 0 ? 'glowing-green medium-text' : 'glowing-silver';
    let btnRareClass =
      scroll === 1 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btnEpicClass =
      scroll === 2 ? 'glowing-pink medium-text' : 'glowing-silver';
    let btnLegendaryClass =
      scroll === 3 ? 'glowing-gold medium-text' : 'glowing-silver';
    let basePrice = 50;
    let stonePrice = 50;
    let buyText = 'presale.scrollcommon';
    if (scroll === 0) {
      buyText = 'presale.scrollcommon';
      if (commonPrice) {
        basePrice = Tron.sunToTrx(commonPrice);
      }
    } else if (scroll === 1) {
      buyText = 'presale.scrollrare';
      if (rarePrice) {
        basePrice = Tron.sunToTrx(rarePrice);
      }
    } else if (scroll === 2) {
      buyText = 'presale.scrollepic';
      if (epicPrice) {
        basePrice = Tron.sunToTrx(epicPrice);
      }
    } else if (scroll === 3) {
      buyText = 'presale.scrolllegendary';
      if (legendaryPrice) {
        basePrice = Tron.sunToTrx(legendaryPrice);
      }
    }

    let cost = sendAmount * basePrice;
    let stoneNumber = cost / stonePrice;

    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="cards">
          <div color="primary" className="card home-card">
            <Typography variant="h4" color="secondary">
              <span className="glowing-pink white">
                <h2>
                  <Translate id="presale.title" />
                </h2>
                <Translate id="presale.subtitle" />
              </span>
            </Typography>
            <div className="presale-info">
              <span className="large-text glowing-green">30</span>{' '}
              <Translate id="presale.description1" />,{' '}
              <img
                src={imgSS}
                alt=<Translate id="general.soulstone" />
                className="image-ss-medium"
              />
              <span className="glowing-gold">
                <Translate id="general.soulstone" />
              </span>{' '}
              <Translate id="general.dividends" />,{' '}
              <Translate id="presale.limitedtime" />{' '}
              <span className="glowing-red">50%</span>{' '}
              <Translate id="presale.discount" />
            </div>
            <div className="card send-card">
              <Grid container spacing={24}>
                <Grid item xs={12} md={6}>
                  <div
                    className="image-placeholder"
                    data-aos="zoom-in"
                    data-aos-delay="50"
                    data-aos-anchor="trigger-scroll"
                    data-aos-anchor-placement="top-center"
                  >
                    <img
                      src={imgScroll}
                      alt="Get Hero"
                      className="image-scroll animate-flicker"
                    />
                  </div>
                </Grid>
                <Grid item xs={12} md={6}>
                  <div className="buy-placeholder">
                    <div>
                      <span className="large-text glowing-blue">
                        {cost} TRX{' '}
                      </span>
                    </div>
                    <div className="space" />
                    <div>
                      <Translate id="presale.scroll" />
                    </div>
                    <div>
                      <Translate id={buyText} />
                    </div>

                    <div className="space" />

                    <div>
                      <Translate id="presale.alsorewards" />
                      <img
                        src={imgSS}
                        alt=<Translate id="general.soulstone" />
                        className="image-ss-small"
                      />
                      <span className="glowing-gold">
                        x{stoneNumber} <Translate id="general.soulstone" />
                      </span>
                    </div>
                    <div className="space" />
                    <Grid container spacing={0}>
                      <Grid item xs={12}>
                        <Button
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.scrollCommon}
                        >
                          <span className={btnCommonClass}>
                            <Translate id="general.common" />
                          </span>
                        </Button>
                        <Button
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.scrollRare}
                        >
                          <span className={btnRareClass}>
                            <Translate id="general.rare" />
                          </span>
                        </Button>
                        <Button
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.scrollEpic}
                        >
                          <span className={btnEpicClass}>
                            <Translate id="general.epic" />
                          </span>
                        </Button>

                        <Button
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.scrollLegendary}
                        >
                          <span className={btnLegendaryClass}>
                            <Translate id="general.legendary" />
                          </span>
                        </Button>
                      </Grid>
                    </Grid>
                    <Grid container spacing={0}>
                      <Grid item xs={12}>
                        <Button
                          id="roll-min-button"
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.roll1}
                        >
                          <span className={btn1Class}>1</span>
                        </Button>
                        <Button
                          id="roll-2x-button"
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.roll5}
                        >
                          <span className={btn5Class}>5</span>
                        </Button>
                        <Button
                          id="roll-2x-button"
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.roll10}
                        >
                          <span className={btn10Class}>10</span>
                        </Button>

                        <Button
                          id="roll-max-button"
                          className="roll-quick-button"
                          variant="contained"
                          size="small"
                          color="primary"
                          onClick={this.roll25}
                        >
                          <span className={btn25Class}>25</span>
                        </Button>
                      </Grid>
                    </Grid>

                    <Grid container spacing={24}>
                      <Grid item xs={12} md={12}>
                        <Button
                          id="send-token-button"
                          variant="contained"
                          color="secondary"
                          size="large"
                          onClick={this.purchaseScroll}
                          disabled={this.isSending()}
                        >
                          <Translate id="presale.buyscroll" />
                        </Button>
                      </Grid>
                    </Grid>
                  </div>
                </Grid>
              </Grid>
            </div>
            <div className="card send-card" id="trigger-intro">
              <Grid container>
                <Grid item xs={12} md={6}>
                  <div className="buy-placeholder">
                    <div>
                      <span className="large-text glowing-pink">
                        <Translate id="general.tronlegend" />
                      </span>
                    </div>
                    <div className="space" />
                    <div>
                      <span className="glowing-pink">
                        <Translate id="general.tronlegend" />
                      </span>{' '}
                      <Translate id="presale.intro1" />.
                    </div>
                    <div>
                      <Translate id="presale.intro2" />.
                    </div>
                    <div>
                      <Translate id="presale.intro3" />{' '}
                      <span className="glowing-blue">
                        <Translate id="presale.intro4" />
                      </span>{' '}
                      <Translate id="presale.intro5" />.
                    </div>
                    <div>
                      <span className="glowing-red large-text">
                        <Translate id="presale.intro6" />
                      </span>
                    </div>
                  </div>
                </Grid>
                <Grid item xs={12} md={6}>
                  <div
                    className="image-placeholder"
                    data-aos="zoom-in"
                    data-aos-delay="50"
                    data-aos-anchor="trigger-intro"
                    data-aos-anchor-placement="top-center"
                  >
                    <img
                      src={imgDragonWarrior}
                      alt="Non Fungible Token"
                      className="image-scroll moveUpDown"
                    />
                  </div>
                </Grid>
              </Grid>
            </div>
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = {
  showSnackbar,
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(PreSale));
