import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';
import TextField from '@material-ui/core/TextField/TextField';
import Button from '@material-ui/core/Button/Button';

import { withLocalize, Translate } from 'react-localize-redux';

import { CopyToClipboard } from 'react-copy-to-clipboard';

import ReactGA from 'react-ga';

import siteTranslations from '../translations/site.json';

import imgTron from '../images/tron-logo.png';
import imgSS from '../images/soulstone.png';
import { showSnackbar } from '../actions/snackbar';

import Tron, { TRON_WEB_STATE_UNLOCKED } from '../utils/tron';

import AOS from 'aos';
import 'aos/dist/aos.css';
import {
  SNACKBAR_ERROR,
  SNACKBAR_INFO,
  SNACKBAR_SUCCESS,
} from './WalletSnackbar';

AOS.init();

const WEB_SITE_URL = 'https://tronlegend.games';

const initialState = {
  stones: 0,
  claimableDividend: 0,
  claimedDividend: 0,
  referral: 0,
  loaded: false,
  accountValidForReferral: false,
};

class MyAccount extends Component {
  constructor(props) {
    super(props);
    this.state = initialState;
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    await this.commonInit();
    window.addEventListener('load', AOS.refresh);

    ReactGA.pageview('/my-account');
  }

  async componentDidUpdate(prevProps) {
    if (this.props.reloadAccountNonce !== prevProps.reloadAccountNonce) {
      this.setState(initialState);
      await this.commonInit();
    }
  }

  async commonInit() {
    const tronWebState = await Tron.initTronWeb();
    if (tronWebState !== TRON_WEB_STATE_UNLOCKED) {
      return;
    }
    await this.loadPlayerData();
  }

  loadPlayerData = async () => {
    const playerData = await Tron.getPlayerData();
    const accountData = await Tron.currentAddressBalance();
    if (accountData > 0) {
      this.setState({ accountValidForReferral: true });
    } else {
      this.setState({ accountValidForReferral: false });
    }
    this.setState({ stones: playerData.stones });
    this.setState({ claimedDividend: playerData.claimedDividend });
    this.setState({ claimableDividend: playerData.claimableDividend });
    this.setState({ referral: playerData.referral });

    this.setState({ loaded: true });
  };

  claimDividend = async () => {
    const { showSnackbar } = this.props;

    try {
      showSnackbar('message.actionInProgress', SNACKBAR_INFO);
      const result = await Tron.claimDividend();
      if (!result) {
        return;
      }
      showSnackbar('message.dividendClaimed', SNACKBAR_SUCCESS);

      await this.loadPlayerData();
      window.addEventListener('load', AOS.refresh);
    } catch (e) {
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_INFO);
      } else {
        if (e.error && e.message) {
          if (
            e.error === 'CONTRACT_VALIDATE_ERROR' &&
            e.message.indexOf('balance is not sufficient') > 0
          ) {
            showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  copyLink = async () => {
    const { showSnackbar } = this.props;
    showSnackbar('message.referralLinkCopied');
  };

  render() {
    const {
      stones,
      claimableDividend,
      claimedDividend,
      referral,
      accountValidForReferral,
      loaded,
    } = this.state;
    const myReferralLink = loaded
      ? window.tronWeb
        ? `${WEB_SITE_URL}?r=${window.tronWeb.defaultAddress.base58}`
        : ``
      : '';
    return (
      <div className="home">
        {loaded ? (
          <div className="cards">
            <div color="primary" className="card home-card">
              <Typography variant="h4" color="secondary">
                <span className="glowing-pink white">
                  <Translate id="general.myaccount" />
                </span>
              </Typography>
              <div className="kingdom-placeholder">
                <Grid container spacing={24}>
                  <Grid item xs={12} md={6} lg={6}>
                    <Grid container spacing={24} className="card">
                      <Grid item xs={12}>
                        <span>
                          <Translate id="general.soulstone" />:{' '}
                          <span className="large-text glowing-gold">
                            {stones}
                          </span>{' '}
                          <img
                            src={imgSS}
                            alt=<Translate id="general.soulstone" />
                            className="image-ss-medium"
                          />
                        </span>
                      </Grid>

                      <Grid item xs={12}>
                        <span>
                          <Translate id="myaccount.claimeddividend" />:{' '}
                          <span className="large-text glowing-blue">
                            {Tron.sunToTrx(claimedDividend)} TRX
                          </span>{' '}
                        </span>
                      </Grid>
                      <Grid item xs={12}>
                        <span>
                          <Translate id="myaccount.claimedreferral" />:{' '}
                          <span className="large-text glowing-blue">
                            {Tron.sunToTrx(referral)} TRX
                          </span>{' '}
                        </span>
                      </Grid>
                      <Grid item xs={12}>
                        <div>
                          <Translate id="myaccount.myreferrallink" /> (
                          <Translate id="myaccount.intro1" />{' '}
                          <span className="glowing-pink">10%</span>{' '}
                          <Translate id="myaccount.intro2" />)
                        </div>

                        {accountValidForReferral ? (
                          <Grid item xs={12}>
                            <div>
                              <TextField
                                label=""
                                name="selectedAsset"
                                className="referral-link-box"
                                value={myReferralLink}
                                margin="normal"
                                color="secondary"
                                variant="outlined"
                              />
                            </div>
                            <div>
                              <CopyToClipboard
                                text={myReferralLink}
                                onCopy={() => {
                                  this.copyLink();
                                }}
                              >
                                <Button
                                  className="roll-quick-button"
                                  variant="contained"
                                  size="small"
                                  color="primary"
                                >
                                  <span className="glowing-red">
                                    <Translate id="myaccount.copylink" />
                                  </span>
                                </Button>
                              </CopyToClipboard>
                            </div>
                          </Grid>
                        ) : (
                          <Grid item xs={12}>
                            <div className="space" />
                            <div>
                              <span className="glowing-red">
                                <Translate id="myaccount.invalidreferral" />
                              </span>
                            </div>
                          </Grid>
                        )}
                      </Grid>
                    </Grid>
                  </Grid>

                  <Grid item xs={12} md={6} lg={6}>
                    <div className="card">
                      <Grid container spacing={24}>
                        <Grid item xs={12}>
                          <div>
                            <Translate id="myaccount.intro3" />{' '}
                            <span className="large-text glowing-blue">
                              {Tron.sunToTrx(claimableDividend)} TRX
                            </span>{' '}
                          </div>
                          <div className="space" />
                          <img
                            src={imgTron}
                            alt=<Translate id="myaccount.claim" />
                            className="image-tron"
                          />
                        </Grid>
                        <Grid item xs={12}>
                          <div className="buy-placeholder">
                            <Grid container spacing={24}>
                              <Grid item xs={12}>
                                <Button
                                  id="send-token-button"
                                  variant="contained"
                                  color="secondary"
                                  size="large"
                                  onClick={this.claimDividend}
                                  disabled={this.isSending()}
                                >
                                  <Translate id="myaccount.claim" />
                                </Button>
                              </Grid>
                            </Grid>
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  </Grid>
                </Grid>
              </div>
            </div>
          </div>
        ) : (
          <div className="cards">
            <div color="primary" className="card home-card">
              <Typography variant="h4" color="secondary">
                <span className="glowing-pink white">
                  <Translate id="general.loading" />
                </span>
              </Typography>
            </div>
          </div>
        )}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  reloadAccountNonce: state.app.reloadAccountNonce,
});

const mapDispatchToProps = {
  showSnackbar,
};

MyAccount.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withLocalize(MyAccount));
