import uuid from 'uuid/v4';

const SET_SCREEN = 'SET_SCREEN';

const SCREEN_PRE_SALE = 'SCREEN_PRE_SALE';
const SCREEN_MY_ACCOUNT = 'SCREEN_MY_ACCOUNT';
const SCREEN_MY_HEROES = 'SCREEN_MY_HEROES';
const SCREEN_SOUL_STONE = 'SCREEN_SOUL_STONE';
const SCREEN_ALL_HEROES = 'SCREEN_ALL_HEROES';
const SCREEN_ITEMS = 'SCREEN_ITEMS';
const SCREEN_PVP = 'SCREEN_PVP';
const SCREEN_PVE = 'SCREEN_PVE';
const SCREEN_PROFESSIONS = 'SCREEN_PROFESSIONS';
const RELOAD_ACCOUNT = 'RELOAD_ACCOUNT';

export {
  SET_SCREEN,
  SCREEN_PRE_SALE,
  SCREEN_MY_ACCOUNT,
  SCREEN_MY_HEROES,
  SCREEN_SOUL_STONE,
  SCREEN_ALL_HEROES,
  SCREEN_ITEMS,
  SCREEN_PVP,
  SCREEN_PVE,
  SCREEN_PROFESSIONS,
  RELOAD_ACCOUNT,
};

export const setScreen = screen => ({
  type: SET_SCREEN,
  payload: {
    screen,
  },
});

export const reloadAccount = () => ({
  type: RELOAD_ACCOUNT,
  payload: {
    reloadAccountNonce: uuid(),
  },
});
